                    style="width: 100px; height: 100px"
                    :src="url"
                    :preview-src-list="srcList">
            </el-image>
        </div>

        <el-descriptions class="margin-top" title="您的信息" :column="3" :size="size" border>
            <template slot="extra">
                <el-button type="primary" size="small">修改个人信息</el-button>
            </template>
<!--user用户-->
            <div v-if="JSON.stringify(this.user)!='{}'">
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p>用户名</p>
                </template>
                {{ user. username}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p>创建</p>
                </template>
                {{ user. createTime}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p>最后登陆</p>
                </template>
                {{ user. lastLoginTime}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-user"></i>
                    <p>名称</p>
                </template>
                {{ user. name}}
            </el-descriptions-item>
            </div>

<!--teacher老师-->
            <div v-if="JSON.stringify(teacher)!='{}'">
            <el-descriptions-item :value-format="yyyy-MM">
                <template slot="label">
                    <i class="el-icon-user"></i>
                    年龄
                </template>
                {{ teacher. age}}
            </el-descriptions-item>
            <el-descriptions-item :value-format="yyyy-MM">
                <template slot="label">
                    <i class="el-icon-user"></i>
                    性别
                </template>
                <p v-if="teacher. sex == 1">男</p>
                <p v-else>女</p>
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-location-outline"></i>
                    email
                </template>
                {{ teacher. email}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-mobile-phone"></i>
                    phone
                </template>
                {{ teacher. phone}}
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-tickets"></i>
                    职称
                </template>
                <el-tag size="small">{{ teacher.teacherPost }}</el-tag>
            </el-descriptions-item>
            </div>
<!--            角色-->
            <div v-if="JSON.stringify(role)!='{}'">
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-tickets"></i>
                    网站角色
                </template>
                <el-tag size="small">{{ role.name }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item>
                <template slot="label">
                    <i class="el-icon-tickets"></i>
                    权限
                </template>
                <el-tag size="small">{{ role.roleDesc }}</el-tag>
            </el-descriptions-item>
            </div>
        </el-descriptions>





<!-- 新增属性 -->
	<el-dialog title="新增学院" :visible.sync="dialogFormVisible1">
	<div style="width:60%;margin: 0 auto">
	<el-form ref="attr" :model="attr" :inline="false" label-width="90px" class="demo-ruleForm">

	<el-form-item label="学院名称" prop="name2" :rules="[{ required: true, message: '请输入学院名称', trigger: 'blur' }]">
		<el-input  type="text" v-model="attr.name2" placeholder="请输入学院名称" auto-complete="off"></el-input>
	</el-form-item>
	<el-form-item label="学院代码" prop="instituteNumber2" :rules="[{ required: true, message: '请输入学院代码', trigger: 'blur' }]">
		<el-input  type="text" v-model="attr.instituteNumber2" placeholder="请输入学院代码" auto-complete="off"></el-input>
	</el-form-item>

	</el-form>
 </div>
	<div slot="footer" class="dialog-footer">
		<el-button @click="dialogFormVisible1 = false">取 消</el-button>
		<el-button @click="resetForm('attr')">重置</el-button>
		<el-button type="primary" @click="submitForm('attr')">确 定</el-button>
	</div>
</el-dialog>

	</el-row>


</template>

<script>
import http from '../../utils/http'
export default {
	data() {
		return {
			filters: {
				keyword1: ''
			},
			attr: {
				name2: '',
				instituteNumber2: ''
			},
			listLoading: false, // 加载等待
			pageSizes1: [30, 50, 80, 100],
			pageSizes2: [5, 10],
			startPage1: 1,
			startPage2: 1,
			pageSize1: 30,
			pageSize2: 5,
			total1: 0,
			total2: 0,
			tableData1: [],
			tableData2: [],
			total1: 0,
			total2: 0,
			page1: 1,
			page2: 1,
			dialogTableVisible1: false,
			dialogFormVisible1: false,
			dialogFormVisible2: false,
			innerVisible: false,
			attrIds: [], // 属性ids集合
			detailIds: [], // 属性明细ids
			attrId: '',
            //展示
            role:{
                id: "2",
                name: "导师",
                roleDesc: "拥有部分权限"
            },
            teacher:{
                age: 1,
                email: "1743225606@qq.com",
                id: "dbca8e5caec24c6ca4b3dccd053f9102",
                name: "高老师",
                phone: "13800138000",
                sex: 2,
                title: "003",
                teacherPost: "讲师"
            },
            user:{
                createTime: "Thu Aug 26 06:54:04 CST 2021",
                enable: 1,
                id: "dbca8e5caec24c6ca4b3dccd053f9102",
                lastLoginTime: "Mon Jan 10 01:45:19 CST 2022",
                name: "name测试21",
                password: "d477887b0636e5d87f79cc25c99d7dc9",
                username: "高老师"
            },
            student:{
                age: 22,
                clazzId: "96332ae13fd64c1abf5f6f778c873102",
                email: "2416292470@qq.com",
                id: "032e7ac5fe4d45ff824c7cf125c70d3f",
                instituteId: "271c18020e6a11ea85a85c93a27933da",
                majorId: "940c25ce0e7111ea85a85c93a27933da",
                name: "张三",
                phone: "12345678910",
                sex: 1,
                studentNumber: "182726103"
            }


		}
	},
	methods: {
		// 查询属性
		async getFormData1 () {
			let _this = this
			_this.listLoading = true
            let user = JSON.parse(sessionStorage.getItem('user'))
			let params = {
                id:user.id,
                roleId:user.roleId,
                roleName:user.roleName,
                username:user.username
			}
			let data = await http.get('account/center', params)

			if(!data.data) {
				_this.listLoading = false
				return
			}

			if (data.data.status === 200) {
                let res = data.data.data.map
                console.log(res)
				 _this.role = res.role
				 _this.teacher = res.teacher
                _this.user = res.user
                _this.teacher.teacherPost = res.teacherPost
                _this.student = res.student

                console.log(JSON.stringify(_this.teacher)!='{}')
			} else {
				_this.message(true,data.data.msg,'error')
				_this.formData1 = []
			}
			_this.listLoading = false
		},
		// 添加属性表单提交
		submitForm(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addInstitute()
				} else {
					console.log('error submit!!');
					return false
				}
			})
		},
		// 重置
		resetForm(formName) {
			this.$refs[formName].resetFields();
		},
		// 新增属性
		async addInstitute() {
			let _this = this
			let params = {
				name: _this.attr.name2,
				instituteNumber: _this.attr.instituteNumber2
			}
			let data = await http.post("institute/add", params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				  _this.resetForm('attr')
				  _this.dialogFormVisible1=false
          _this.message(true,data.data.msg,'success')
					_this.getFormData1()
			} else {
         _this.message(true,data.data.msg,'error')
			}
		},
		// 删除属性提示
		async delAttr () {
			if (this.attrIds.length === 0) {
				 this.message(true,'请选择需要删除的学院','warning')
				 return
			}
			this.$confirm('此操作将永久删除, 是否继续?', '提示', {
				confirmButtonText: '确定',
				cancelButtonText: '取消',
				type: 'warning'
			}).then(() => {
				this.delAttrs()
			}).catch(() => {
				this.message(true,'已取消删除','warning')
			})
		},
		// 删除属性
		async delAttrs() {
      let _this = this
			let params = {
				ids: _this.attrIds
			}
			let data =await http.post('institute/delete', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.message(true,data.data.msg,'success')
			} else {
				 _this.message(true,data.data.msg,'error')
			}
			_this.getFormData1()
		},
		// 获取选中集
		handleSelectionChange1(val) {
			this.attrIds = []
			if (val) {
			 val.forEach(row => {
				 this.attrIds.push(row.id)
			 });
		 }
		},
		// 每页大小改变时触发
		handleSizeChange1 (val) {
			this.pageSize1 = val
			this.getFormData1()
		},
		// 当前页码改变时触发
		handleCurrentChange1 (val) {
			this.startPage1 = val
			this.getFormData1()
		},

		// 属性明细表单提交
		submitForm1(formName) {
			this.$refs[formName].validate((valid) => {
				if (valid) {
					this.addDetail()
				} else {
					console.log('error submit!!');
					return false
				}
			})
		},
		// 添加属性明细
		async addDetail() {
			 let _this = this
			 let params = {
				 id: _this.attrId,
				 name: _this.detail.name
			 }
			 let data = await http.post('SysApi/v1/addAttributeDetail', params)

			 if(!data.data) {
			 	return
			 }

			 if (data.data.status === 200) {
 				  _this.resetForm('detail')
           _this.message(true,data.data.msg,'success')
 					_this.selDetails(this.attrId)
 			} else {
          _this.message(true,data.data.msg,'error')
 			}
		},
		// 查询属性明细
		async selDetails(id) {
			let _this = this
			_this.dialogTableVisible1 = true
			_this.attrId = id
			let params = {
				id: id,
				pageSize: _this.pageSize2,
				startPage: _this.startPage2
			}
			let data = await http.get('SysApi/v1/findAttributesDetailByPage', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.tableData2 = data.data.data
				 _this.total2 = data.data.total
			} else {
				_this.message(true,data.data.msg,'error')
				_this.formData2 = []
			}
		},
		// 删除属性明细
		async delDetails() {
      let _this = this
			if (_this.detailIds.length === 0) {
				 this.message(true,'请选择需要删除的属性明细','warning')
				 return
			}

			let params = {
				ids: _this.detailIds
			}
			let data =await http.post('SysApi/v1/delAttributeDetails', params)

			if(!data.data) {
				return
			}

			if (data.data.status === 200) {
				 _this.message(true,data.data.msg,'success')
			} else {
				 _this.message(true,data.data.msg,'error')
			}
			_this.selDetails(this.attrId)
		},
		// 获取选中集
		handleSelectionChange2(val) {
			this.detailIds = []
			if (val) {
			 val.forEach(row => {
				 this.detailIds.push(row.id)
			 });
		 }
		},
		// 每页大小改变时触发
		handleSizeChange2 (val) {
			this.pageSize2 = val
			this.selDetails(this.attrId)
		},
		// 当前页码改变时触发
		handleCurrentChange2 (val) {
			this.startPage2 = val
			this.selDetails(this.attrId)
		},
		/**
		 * ifshow: true/false msg: message  type: error/warning/success
		 */
		message(ifshow,msg,type) {
			this.$message({
				showClose: ifshow,
				message: msg,
				type: type
			})
		}
	},
	mounted() {
		this.getFormData1()
	}
}
</script>



